.change-password__container {
  @include desk {
    padding: 0px;
    position: relative;
    height: 780px;
  }
}

.change-password__form-wrapper {
  padding: 40px 16px;
  background-color: getColor('white');

  @include desk {
    width: 636px;
    height: 449px;
    padding: 40px;
    margin-left: 72px;
    border: 1px solid #0e1012;
  }
}

.change-password__heading {
  text-align: center;
  @include font(700, 28px, 36px, null);

  @include desk {
    @include font(700, 32px, 40px, null);
  }
}

.change-password__text {
  text-align: center;
  margin-top: 24px;
  margin-bottom: 32px;
  @include font(400, 16px, 24px, null);

  @include desk {
    margin-top: 40px;
    margin-bottom: 40px;
  }
}

.change-password__email-input {
  width: 343px;
  height: 48px;
  margin-bottom: 32px;

  @include desk {
    width: 556px;
  }
}

.change-password__btn {
  width: 343px;
  height: 48px;
  padding: 10px 16px;
  background-color: getColor('black');
  color: getColor('white');
  border: 1px solid #0e1012;
  opacity: 0.5;
  @include font(500, 16px, 24px, null);

  @include desk {
    width: 556px;
    @include font(500, 16px, 16px, null);
  }
}

